<template>
  <div>
    <el-table :data="tableData" class="list" v-loading="loading">
      <el-table-column prop="id" label="编号" align="center" />
      <el-table-column prop="name" label="姓名" align="center" />
      <el-table-column prop="mobile" label="电话" align="center" />
      <el-table-column prop="type_name" label="类型" align="center" />
      <el-table-column prop="content" label="反馈内容" align="center" />
      <el-table-column prop="image" width="400" label="图片" align="center">
        <template slot-scope="scope">
          <img class="img" :src="scope.row.image" alt="" />
        </template>
      </el-table-column>
      <el-table-column prop="reply_time" label="回复时间" align="center" />
      <el-table-column prop="reply_content" label="回复内容" align="center" />
      <el-table-column prop="addtime_date" label="添加时间" align="center" />
      <el-table-column prop="address" align="center" label="操作" width="150">
        <template slot-scope="scope">
          <el-button @click="editItem(scope.row)" type="primary" size="small">回复</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="total, prev, pager, next"
      :total="count"
      :current-page="page_curren"
      :hide-on-single-page="false"
      @current-change="currentChange"
      class="pagination"
    />
    <el-dialog title="回复" :visible.sync="dialogFormVisible">
      <el-form :model="detail">
        <el-form-item label="请输入回复内容" label-width="80">
          <el-input type="textarea" v-model="reply_content"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="replySubmit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'

export default {
  data() {
    return {
      dialogFormVisible: false,
      config: {
        cats: {},
      },
      reply_content: '',
      loading: false,
      detail: {},
      all_money: '',
      tableData: [],
      count: 0,
      page_curren: 1,
      addtime: [],
      reply_id: '',
    }
  },
  // watch: {
  //   addtime(val) {
  //     if(val) {
  //       this.searchs.begin_addtime = dayjs(val[0]).format('YYYY-MM-DD')
  //       this.searchs.end_addtime = dayjs(val[1]).format('YYYY-MM-DD')
  //     } else {
  //       this.searchs.begin_addtime = ''
  //       this.searchs.end_addtime = ''
  //     }
  //   }
  // },
  mounted() {
    this.FetchList()
  },
  methods: {
    FetchList() {
      this.loading = true
      Fetch('/user/replyback/search', {
        page_curren: this.page_curren,
      })
        .then(r => {
          this.tableData = r.data.list
          this.count = r.data.count
          this.page_curren = Math.floor(r.data.page_curren)
        })
        .catch(res => {
          Message.error(res.msg)
        })
      this.loading = false
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
    replySubmit() {
      this.loading = true
      Fetch('/user/replyback/reply', {
        reply_id: this.reply_id,
        reply_content: this.reply_content,
      })
        .then(r => {
          this.dialogFormVisible = false
          this.FetchList()
          Message.success('回复成功')
        })
        .catch(res => {
          Message.error(res.msg)
        })
      this.loading = false
    },
    onSubmit() {
      this.page_curren = 1
      this.FetchList()
    },
    editItem(row) {
      this.dialogFormVisible = true
      this.reply_id = row.id
      if (row.reply_content) {
        this.reply_content = row.reply_content
      } else {
        this.reply_content = ''
      }
    },
    removeItem(row) {
      this.$confirm('是否确认删除此条数据?', undefined, {
        type: 'warning',
        beforeClose: (action, instance, done) => {
          if (action === 'confirm') {
            instance.confirmButtonLoading = true
            Fetch('/article/art/remove', { id: row.id })
              .then(() => {
                instance.confirmButtonLoading = false
                Message.success('删除成功')
                done()
                this.FetchList()
              })
              .catch(() => {
                instance.confirmButtonLoading = false
                Message.error('删除失败')
              })
          } else {
            done()
          }
        },
      })
    },
  },
}
</script>
